/*PEN STYLES*/
* {
  box-sizing: border-box;
}

body {
  background: #556270;
  margin: 0;
}

input[type=search] {
  width: 40vh !important
}

.none {
  display: none;
}

.post {
  background: #fff;
}

.title {
  font-weight: bold;
}

.jumbotron {
  background-color: #fafafa !important;
  margin-top: 5%;
  box-shadow: 0 3px 10px -1px rgba(0, 0, 0, 0.8);
}

.lead {
  margin-top: 3%;
  margin-bottom: 3%;
  font-weight: 400;
  text-align: justify;
}

.post-img {
  float: left;
  margin-right: 3%;
  margin-top: 3%;
  max-width: 40%;
  border-radius: 8px;
}

.img-about {
  margin-right: 3%;
  margin-top: 3%;
  max-width: 40%;
  border-radius: 100px;
}

.navbar {
  padding: 0rem 4rem;
  box-shadow:1px 1px 10px #000000
}

.nav-padding {
  padding: 1.2rem 1.5rem 1.3rem 1.5rem;
  margin: 0;
  transition: 0.4s
}

.nav-padding:hover {
  background: #2a9d8f;
}

.nav-link {
  font-size: 20px;
}

.nav-link:hover {
  color: #fff !important;
}

.dropdown-item {
  font-size: 19px;
  padding: 0.5rem 2rem;
  margin-right: 5rem
}

.dropdown-item:hover {
  background: #f1f1f1;
}

.bg-light {
  background: #fff !important;
}

.newsletters {
  color: white;
  padding-top: 2rem;
}

.blog-card {
  display: flex;
  flex-direction: column;
  margin: 3rem auto;
  box-shadow: 0 3px 10px -1px rgba(0, 0, 0, 0.5);
  margin-bottom: 1.6%;
  background: #fff;
  line-height: 1.4;
  font-family: sans-serif;
  border-radius: 5px;
  overflow: hidden;
  z-index: 0;
}
.blog-card a {
  color: inherit;
}
.blog-card a:hover {
  color: #5ad67d;
}
.blog-card h1 a {
  text-decoration: none;
}
.blog-card:hover .photo {
  -webkit-transform: scale(1.3) rotate(1deg);
          transform: scale(1.3) rotate(1deg);
}
.blog-card .meta {
  position: relative;
  z-index: 0;
  height: 200px;
}
.blog-card .photo {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-size: cover;
  background-position: center;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}
.blog-card .details,
.blog-card .details ul {
  margin: auto;
  padding: 0;
  list-style: none;
}
.blog-card .details {
  position: absolute;
  top: 0;
  bottom: 0;
  left: -100%;
  margin: auto;
  transition: left .2s;
  background: rgba(0, 0, 0, 0.6);
  color: #fff;
  padding: 10px;
  width: 100%;
  font-size: .9rem;
}
.blog-card .details a {
  -webkit-text-decoration: dotted underline;
          text-decoration: dotted underline;
}
.blog-card .details ul li {
  display: inline-block;
}
.blog-card .details .author:before {
  margin-right: 10px;
}
.blog-card .details .date:before {
  margin-right: 10px;
}
.blog-card .details .tags ul:before {
  margin-right: 10px;
}
.blog-card .details .tags li {
  margin-right: 2px;
}
.blog-card .details .tags li:first-child {
  margin-left: -4px;
}
.blog-card .description {
  padding: 1rem;
  background: #fff;
  position: relative;
  z-index: 1;
}
.blog-card .description h1,
.blog-card .description h2 {
  font-family: Poppins, sans-serif;
}
.blog-card .description h1 {
  line-height: 1;
  margin: 0;
  font-size: 1.7rem;
}
.blog-card .description h2 {
  font-size: 1rem;
  font-weight: 300;
  text-transform: uppercase;
  color: #a2a2a2;
  margin-top: 5px;
}
.blog-card .description .read-more {
  text-align: right;
}
.blog-card .description .read-more a {
  color: #5ad67d;
  display: inline-block;
  position: relative;
}
.blog-card .description .read-more a:after {
  margin-left: -10px;
  opacity: 0;
  vertical-align: middle;
  transition: margin .3s, opacity .3s;
}
.blog-card .description .read-more a:hover:after {
  margin-left: 5px;
  opacity: 1;
}
.blog-card p {
  position: relative;
  margin: 1rem 0 0;
}
.blog-card p:first-of-type {
  margin-top: 1.25rem;
}
.blog-card p:first-of-type:before {
  content: "";
  position: absolute;
  height: 5px;
  background: #5ad67d;
  width: 35px;
  top: -0.75rem;
  border-radius: 3px;
}
.blog-card:hover .details {
  left: 0%;
}

.readmore{
  box-shadow: 0 3px 10px -1px rgba(0, 0, 0, 0.5);
  margin-top: 10px;
  width: 200px;
  height: 55px;
  font-size: 25px
}

@media (min-width: 640px) {
  .blog-card {
    flex-direction: row;
    max-width: 1000px;
  }
  .blog-card .meta {
    flex-basis: 40%;
    height: 250px;
  }
  .blog-card .description {
    flex-basis: 60%;
  }
  .blog-card .description:before {
    -webkit-transform: skewX(-3deg);
            transform: skewX(-3deg);
    content: "";
    background: #fff;
    width: 30px;
    position: absolute;
    left: -10px;
    top: 0;
    bottom: 0;
    z-index: -1;
  }
  .blog-card.alt {
    flex-direction: row-reverse;
  }
  .blog-card.alt .description:before {
    left: inherit;
    right: -10px;
    -webkit-transform: skew(3deg);
            transform: skew(3deg);
  }
  .blog-card.alt .details {
    padding-left: 25px;
  }
}

/* ---- Social Media ---- */
.wrapper {
	position: fixed;
	top: 50%;
	left: 3%;
	transform: translate(-50%,-50%);
}
.social-media {
	list-style: none;
}
.social-media .li-social {
	width: 40px;
	height: 40px;
	background: #e59500;
	margin: 10px 0;
	border-radius: 3px;
	position: relative;
	cursor: pointer;
	box-shadow: 0 0 10px rgba(0,0,0,0.3);
}
.social-media li .fa {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
  font-size: 20px;
  color: #fff
}
.social-media li.facebook {
	background: #3b5999;
}
.social-media li.twitter {
	background: #55acee;
}
.social-media li.instagram {
	background: #e4405f;
}
.social-media li.google {
	background: #dd4b39;
}
.social-media li.whatsapp {
	background: #25d366;
}
.slider {
	position: absolute;
	top: 0;
	left: 51px;
	width: 0px;
	height: 50px;
	line-height: 50px;
	background: #eebb5c;
	text-align: center;
	border-radius: 3px;
	transition: all 0.2s ease;
}
.slider p {
	text-transform: uppercase;
	font-size: 16px;
	opacity: 0;
	font-weight: 900;
	transition: all 0.2s ease;
}
.social-media .li-social.facebook div.slider {
	background: #3b5999;
}
.social-media .li-social.twitter div.slider {
	background: #55acee;
}
.social-media .li-social.instagram div.slider {
	background: #e4405f;
}
.social-media .li-social.google div.slider {
	background: #dd4b39;
}
.social-media .li-social.whatsapp div.slider {
	background: #25d366;
}
.social-media .li-social:hover .slider {
	width: 180px;
}
.social-media .li-social:hover .slider p {
	opacity: 1;
}

/* ---- Footer ---- */
section {
  padding: 30px 0;
  margin-top: 3rem;
}

section .section-title {
  text-align: center;
  color: #007b5e;
  margin-bottom: 50px;
  text-transform: uppercase;
}
#footer {
  background: #007b5e !important;
}
#footer h5{
	padding-left: 10px;
  border-left: 2px solid #eeeeee;
  padding-bottom: 6px;
  margin-bottom: 20px;
  color:#ffffff;
}
#footer a {
  color: #ffffff;
  text-decoration: none !important;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}
#footer ul.social li{
	padding: 3px 0;
}
#footer ul.social li a i {
  margin-right: 5px;
	font-size:25px;
	-webkit-transition: .5s all ease;
	-moz-transition: .5s all ease;
	transition: .5s all ease;
}
#footer ul.social li:hover a i {
	font-size:30px;
	margin-top:-10px;
}
#footer ul.social li a,
#footer ul.quick-links li a{
	color:#ffffff;
}
#footer ul.social li a:hover{
	color:#eeeeee;
}
#footer ul.quick-links li{
	padding: 3px 0;
	-webkit-transition: .5s all ease;
	-moz-transition: .5s all ease;
	transition: .5s all ease;
}
#footer ul.quick-links li:hover{
	padding: 3px 0;
	margin-left:5px;
	font-weight:700;
}
#footer ul.quick-links li a i{
	margin-right: 5px;
}
#footer ul.quick-links li:hover a i {
  font-weight: 700;
}

/* ------ Contact Form ------  */
.contact-form{
  background: #fff;
  box-shadow: 0 3px 10px -1px rgba(0, 0, 0, 0.5);
  margin-top: 5%;
  margin-bottom: 3%;
  width: 70%;
}
.contact-form .form-control{
  border-radius:1rem;
}
.contact-image{
  text-align: center;
}
.contact-image img{
  background-color: #fff;
  border-radius: 6rem;
  padding-right: 5px;
  width: 15%;
  margin-top: -3%;
  transform: rotate(0deg);
}
.contact-form form{
  padding: 14% 5% 11% 5%;
}
.contact-form form .row{
  margin-bottom: -7%;
}
.contact-form h3{
  margin-bottom: 8%;
  margin-top: -15%;
  text-align: center;
  color: #0062cc;
}
.contact-form .btnContact {
  width: 25%;
  border: none;
  box-shadow: 0 3px 10px -1px rgba(0, 0, 0, 0.5);
  border-radius: 1rem;
  margin-top: 10%;
  margin-bottom: -5%;
  padding: 1.5%;
  background: #dc3545;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  transition: 0.3s;
}
.contact-form .btnContact:hover {
 background: #B22222;
}
.btnContactSubmit
{
  width: 50%;
  border-radius: 1rem;
  padding: 1.5%;
  color: #fff;
  background-color: #0062cc;
  border: none;
  cursor: pointer;
}

.txtMsg {
  width: 100%;
  height: 150px;
}

/* ------ Responsive-CSS ------ */

@media (max-width:767px){
	#footer h5 {
    padding-left: 0;
    border-left: transparent;
    padding-bottom: 0px;
    margin-bottom: 10px;
  }
}

@media (min-width:967px) {
  .social-footer {
    display: none;
  }
}

@media (max-width:967px) {
  .newsletters {
    font-weight: bold;
    font-size: 190%
  }
  .social-media {
    display: none;
  }
  .post-img {
    max-width: 100%;
  }
  .img-about {
    max-width: 100%;
  }
  .contact-image img {
    width: 100px
  }
  .contact-form .btnContact {
    width: 200px;
  }
  .contact-form form {
    padding: 14% 5%; 
  }
}